import { Web3Storage } from 'web3.storage'
const makeFileObjects =(obj,name)=> {
    const blob = new Blob([JSON.stringify(obj)], {type : 'application/json'})
    const files = [
      new File([blob], name)
    ]
    return files
  }
export const uploadStorage = async (values, files) => {
    const client = new Web3Storage({ token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiJkaWQ6ZXRocjoweEY0OTI5NTBiRjY2NTkwMDRhQzk5RmVjYzdFMzYzZTgxMDMwNEFiMzMiLCJpc3MiOiJ3ZWIzLXN0b3JhZ2UiLCJpYXQiOjE2NDI3NjE5MTg1NzQsIm5hbWUiOiJ3d3cifQ.P-mddGCDiII2x1DpW8w2Jogbsbk_2emanrB11ronZ5k' })
        
    // post file to IPFS, get the IPFS hash and store it in contract
    return new Promise(async function (resolve, reject) {
      try {
        // cid
        const imgCid = await client.put(files)
        const imgRes = await client.get(imgCid)
        const imgFile = await imgRes.files()
        const dataStr = {
            ...values,
            image: `https://ipfs.io/ipfs/${imgFile[0].cid}`
        }
        // jsoncid
        const objFile = makeFileObjects(dataStr,'metadata')
        const objCid = await client.put(objFile)
        const objRes = await client.get(objCid)
        const file = await objRes.files()
        resolve(`https://ipfs.io/ipfs/${file[0].cid}`);
      } catch (error) {
        console.error(error);
        reject(error);
      }
    });
  };
